<template>
  <div style="display: flex">
    <div class="leftDiv">
      <h3 style="">待处理货柜信息</h3>
      <div>
        渠道
        <el-select v-model="ditch" placeholder="请选择">
          <el-option
            v-for="item in ditchList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        站点
        <el-select v-model="site" placeholder="请选择">
          <el-option
            v-for="item in siteList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        国家
        <el-select v-model="country" placeholder="请选择">
          <el-option
            v-for="item in countryList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        货柜号搜索
        <el-input
          v-model="container"
          placeholder="请输入内容"
          style="width: 8.8vw"
        ></el-input>
      </div>
      <div>
        <el-button
          type="primary"
          style="width: 8vw; margin-left: 2vw"
          @click="searchContainer()"
          >搜 索</el-button
        >
      </div>
      <div class="UlDive">
        <ul class="leftUl">
          <li
            v-for="(item, index) in searchList"
            :key="index"
            class="containerLi"
            @click="showDetail(item.id, index)"
          >
            {{ item.container_num }}-{{ item.factory }}
          </li>
        </ul>
        <ol v-if="flag2">
          <li>没找到货柜号</li>
        </ol>
      </div>
    </div>
    <div style="flex: 7; padding-left: 1vw">
      <h3 style="text-indent: 28vw; color: white">
        工厂+货柜编号+站点+国家+FBA/海外仓 运营资料上传
      </h3>
      <table>
        <thead>
