                    getFileUrl(tableList.calculation_file_path) +
                    '&filename=' +
                    getfilename(tableList.calculation_file_path)
                "
                download
                >{{ getfilename(tableList.calculation_file_path) }}</a
              >
            </td>
            <td>{{ tableList.calculation_file_date }}</td>
            <td>{{ tableList.calculation_file_state }}</td>
            <td></td>
          </tr>
          <tr>
            <td>下单表</td>
            <td>
              <a
                :href="
                  'https://www.beyoung.group/file_download/?path=' +
