<style scoped>
table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 60vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 6vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 40px;
  line-height: 40px;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 40px;
  line-height: 40px;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

.leftDiv {
  flex: 1.5;
  height: 91.8vh;
  border-right: 1px solid #dddddd;
  background: rgba(255, 255, 255, 0.9);
}

.leftDiv h3 {
  text-indent: 1em;
}

.leftDiv div {
  margin: 0.5vh 0.3vw;
}

.leftUl {
  margin-left: 0.3vw;
  width: 13.8vw;
}
.leftUl li {
  border: 1px #dddddd solid;
  text-align: center;
}

table tr th:nth-child(1),
table tr td:nth-child(1) {
  width: 12vw;
}
table tr th:nth-child(2),
table tr td:nth-child(2) {
  width: 28vw;
}
table tr th:nth-child(3),
table tr td:nth-child(3) {
  width: 21vw;
}
table tr th:nth-child(4),
table tr td:nth-child(4) {
  width: 11vw;
}
table tr th:nth-child(5),
table tr td:nth-child(5) {
  width: 8.4vw;
}

.UlDive {
  max-height: 57vh;
  overflow-y: auto;
}
</style>



